.calculator {
  padding-top: 180px;
  padding-bottom: 120px;
}

@media  screen and (max-width: 1283px) {
  .calculator {
    padding-top: 206px;
  }
}

@media  screen and (max-width: 1217px) {
  .calculator {
    padding-top: 230px;
  }
}

@media  screen and (max-width: 1200px) {
  .calculator {
    padding-top: 210px;
  }
}

@media  screen and (max-width: 1100px) {
  .calculator {
    padding-top: 144px;
  }
}

@media  screen and (max-width: 630px) {
  .calculator {
    padding-top: 96px;
  }
}

@media  screen and (max-width: 600px) {
  .calculator {
    padding-top: 92px;
  }
}

.calculator__header {
  margin-top: 24px;
  margin-bottom: 40px;
}

.calculator__header h1 {
  margin: 0;
  margin-bottom: 16px;

  font-weight: 800;
  font-size: 36px;
  line-height: 40px;
  text-transform: uppercase;
  color: $color-title;
}

@media  screen and (max-width: 540px) {
  .calculator__header h1 {
    font-size: 30px;
    line-height: 33px;
  }
}

.calculator__header h2 {
  max-width: 155px;
  margin: 0;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 12px;
  padding-right: 12px;

  font-size: 20px;
  line-height: 24px;
  color: $color-title;

  background-color: $color-grey-background;
}

@media  screen and (max-width: 768px) {
  .calculator__header h2 {
    max-width: 254px;
    padding-right: 111px;

    color: $color-text;
  }
}

.calculator__description {
  margin: 0;
  margin-bottom: 60px;

  font-size: 16px;
  line-height: 22px;
  color: $color-text;
}

.calculator__slider-wrapper {
  display: flex;
  justify-content: initial;
  flex-wrap: nowrap;
  margin: 0;
  padding: 0;
  margin-bottom: 48px;


  list-style: none;
}

.calculator__item {
  display: flex;
  flex-shrink: 0;
}

.calculator__link {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 14px;
  padding-left: 27px;
  padding-right: 27px;

  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: $color-text;

  background-color: $color-grey-background;
}

.calculator__link:hover {
  color: $color-general;
}

.calculator__link.active {
  color: $color-default-white;

  background-color: $color-general;
  box-shadow: 0px 13px 12px -10px rgba(229, 36, 33, 0.25);
}

.calculator__link.active:hover {
  color: $color-default-white;

  cursor: default;
}

.block {
  display: none;
}

.block.active {
  display: block;
}

.calculator__title {
  position: relative;
}

.calculator__title::before {
  content: "";

  position: absolute;

  top: -5px;
  left: -40px;

  width: 219px;
  height: 52px;

  background-image: url(../img/svg/rectangle-10.svg);
  background-repeat: no-repeat;
}

.calculator__title h2 {
  position: relative;
  font-weight: 800;
  font-size: 32px;
  line-height: 35px;
  color: $color-title;

  z-index: 1;
}

.calculator__title h2 span {
  color: $color-general;
}

.calculator__btns {
  display: flex;
  justify-content: flex-end;
  max-width: 95px;
  margin-top: 8px;
  margin-left: auto;
}

.calculator__btns button {
  position: relative;
  display: block;
  width: 42px;
  height: 42px;
  padding: 0;

  border: none;
  box-shadow: none;
  background-color: transparent;
  border-radius: 100%;
  cursor: pointer;
}

.calculator__btn-print {
  margin-right: 8px;
}

.calculator__btns button:hover {
  background-color: $color-grey-background;
}

.calculator__btn-print::before {
  content: "";

  position: absolute;

  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  width: 24px;
  height: 24px;

  background-image: url(../img/svg/print.svg);
  background-repeat: no-repeat;
}

.calculator__btn-share::before {
  content: "";

  position: absolute;

  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  width: 18px;
  height: 20px;

  background-image: url(../img/svg/share.svg);
  background-repeat: no-repeat;
}
