.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  background-color: rgba(35, 38, 45, 0.6);
  backdrop-filter: blur(12.5px);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px; }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 600px;
    background-color: #FFFFFF; }
  .modal__close-btn {
    position: absolute;
    top: 16px;
    right: 16px; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    display: none; }
  .modal--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

.modal--call .modal__content {
  max-width: 390px;
  background-color: #F5F6FA; }

@media screen and (max-width: 768px) {
  .modal--call .modal__content {
